import Vue from 'vue'
import Vuex from 'vuex'

import actions from './actions'
import mutations from './mutations'
import getters from './getters'

Vue.use(Vuex)

const state = {
  collections: [],
  books: []
}

const store = new Vuex.Store({
  state,
  mutations,
  actions,
  getters
})


if (module.hot) {
  store.hotUpdate({
    state: {
      collections: [],
      books: []
    },
    mutations,
    actions,
    getters
  })
}

export default store



